import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function priceToEarningsRatioTtm(tableName: string) {
  const title = "P/E Ratio (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["priceToEarningsRatioTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Price per share vs. earnings.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about price to earnings ratio?`,
              value: info.row.original.financialData.companyRatios?.data?.priceToEarningsRatioTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyRatios?.data?.priceToEarningsRatioTtm}
        />
      )
    },
  })
}

export { priceToEarningsRatioTtm }
